@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap);
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    font-family: 'Poppins', 'sans-serif';
}

.container {
    max-width: 500px;
    margin: 30px auto;
    overflow: auto;
    min-height: 300px;
    border: 1px solid steel blue;
    padding: 30px;
    border-radius: 5px;
}

.header {
    display: flex;
    padding: 10px 20px;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;

}

.btn {
    display: inline-block;
    background: black;
    color: #fff;
    border: none;
    padding: 10px 20px;
    margin: 5px;
    border-radius: 5px;
    cursor: pointer;
    text-decoration: none;
    font-size: 15px;
    font-family: inherit;
}

.btn-focus {
    outline: none;
}

.btn-active {
    -webkit-transform: scale(0.98);
            transform: scale(0.98);
}

.btn-block {
    display: block;
    margin: 10px;
    width: 98%;
}

.task {
    background: #f4f4f4;
    margin: 15px;
    padding: 10px 20px;
    cursor: pointer;
   
}

.reminder {
    border-left: 5px solid green;
}

.h3 {
    display: flex;
    align-items: left;
    justify-content: space-between;
}

.add-form {
    margin-bottom: 40px;
}

.form-control {
    
    padding: 10px 20px;
    margin: 20px 0;
}

.form-control label {
    display: block;
}

.form-control-check {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.form-control-check-label {
    flex: 1 1;
}

.form-control-check-input {
    flex: 2 1;
    font-family: sans-serif;
    margin: 2px;
    border-radius: 2px;
    width: 100%;
    height: 40px;
    
}

.footer {
    margin-top: 30px;
    text-align: center;
}

